// app/apollo/operations/auth.ts
import { gql } from '@apollo/client';
import client, { isLoggedInVar, authTokenVar, currentUserVar } from '@/app/apollo/client';

// Define proper types
interface User {
  id: string;
  role: string;
  email: string;
  firstName?: string;
  lastName?: string;
}

// Client-side query to check login status
export const IS_LOGGED_IN = gql`
  query IsUserLoggedIn {
    isLoggedIn @client
    currentUser @client
  }
`;

// Login mutation
export const LOGIN = gql`
  mutation Login($email: String!, $password: String!) {
    login(input: { email: $email, password: $password }) {
      token
      user {
        id
        role
        email
        firstName
        lastName
      }
    }
  }
`;

export interface LoginResponse {
    login: {
      token: string;
      user: User;
    }
  }

// Helper functions with proper type annotations
export const login = (token: string, user: User): void => {
  authTokenVar(token);
  currentUserVar(user);
  isLoggedInVar(true);
};

export const logout = (): void => {
  authTokenVar('');
  currentUserVar(null);
  isLoggedInVar(false);
  
  // Clear Apollo cache on logout
  client.clearStore().catch(err => {
    console.error('Error clearing Apollo cache:', err);
  });
};